






















import Vue from "vue";
import AwardBox from "../../components/Page/AwardBox.vue";
export default Vue.extend({
	name: "Awards",
	components: {
		AwardBox
	},
	data() {
		return {
			awards: [] as any
		};
	},
	created() {
		this.$store
			.dispatch("GET_CLUB_AWARDS")
			.then(awards => {
				this.awards = awards;
			})
			.catch(err => {});
	},
	methods: {
		appendAwards() {
			this.awards.push({
				title: "",
				subtitle: "",
				target: [],
				level: "",
				isCreated: true
			});
		}
	},
	computed: {
		getClub() {
			return this.$store.state.club;
		},
		getClubImage() {
			if (this.$store.state.club.imgPath)
				return (
					this.$store.state.mainPath + this.$store.state.club.imgPath
				);
			else
				return "https://pbs.twimg.com/profile_images/770139154898382848/ndFg-IDH_400x400.jpg";
		},
		isAdmin() {
			if (this.$store.state.club.ranks) {
				let rank = this.$store.state.club.ranks.find(
					(rank: any) =>
						rank.user == this.$store.state.userInformation._id
				);
				if (rank) return rank.isAdmin;
				else return false;
			} else return false;
		}
	}
});
