

































import Vue from "vue";
export default Vue.extend({
	props: {
		data: Object,
		admin: Boolean
	},
	created() {},
	methods: {
		appendTarget() {
			this.data.target.push({
				name: "",
				user: ""
			});
			this.$nextTick(() => {
				let last = this.$refs.last as HTMLInputElement[];
				if (last) {
					last[0].focus();
				}
			});
		},
		pressEnter(e: any, idx: number) {
			if (this.data.target.length - 1 == idx && e.keyCode == 13)
				this.appendTarget();
		}
	}
});
